.card {
  display: flex;
  background-color: #fff;
  color: black;

  
  min-height: 100px;
  min-width: 100px;
  border-radius: 4px;
  
  text-decoration: none;
  
}
.card-company{
  border-radius: 4px;
  padding: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem;
  
  &:hover{
    background-color: rgb(189, 189, 189);
  }
}